import React, { useState } from "react";
import { Link } from "react-router-dom";

import "../../css/Registration.css";
import "../../css/Login.css";

function Registration() {
  return (
    <div className="all-container">
      <div className="body-container">
        <div className="reg_form_container">
          <form encType="multipart/form-data" className="reg_form">
            <h2>Регистрация</h2>
            <div className="input_group">
              <input
                type="email"
                placeholder="Почта"
                className="input_text"
                autoComplete="off"
              />
            </div>
            <div className="input_group">
              <input
                type="username"
                placeholder="Логин"
                className="input_text"
                autoComplete="off"
              />
            </div>
            <div className="input_group">
              <input
                type="password"
                placeholder="Пароль"
                className="input_text"
                autoComplete="off"
              />
            </div>
            <div className="button_group" id="reg_button">
              <Link to="/creatch">Подтвердить</Link>
            </div>
            <div className="fotterReg">
              <Link to="/creatch">Продолжить без регистрации</Link>
              <Link to="/login">Вход</Link>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Registration;
